ul.guessBox {
    height: 55px;
    margin-left: auto;
    margin-right: auto;
    background: #11a8ab;
    padding: 0.5em;
    display: block;
    line-height: 2em;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    color: #fff;
    overflow: auto;
  width:50%;
  max-width:500px
}
ul.guessBox li {
    display: inline;
    background-color: #1a4e95;
    padding: 0.3em;
    border-radius: 4px;
    width: 95%;
    margin: 0.2em;
    color: #fff;
}



